<template>
<nav>
  <a href="/addImage"  class="add">Add Image</a>
  <div id="map"></div>


</nav>
</template>

<script>
import axios from 'axios'
import YandexMap from '../mapScript'

export default {

  data() {
    return {
      categories: '',
      error: false,
      coords: [],
      uploadedImage: ''
    }
  },
  mounted() {
    this.$nextTick(function () {
      ymaps.ready(() => {
        const myMap = new ymaps.Map('map', {
          center: [53.893009, 27.567444],
          zoom: 10
        });
        myMap.events.add('click', (e) => {
          if (!myMap.balloon.isOpen()) {
            this.coords = e.get('coords');
            myMap.balloon.open(this.coords, {
              contentHeader: 'Photo was taken here!',
              contentBody:
                '<p>Coordinates: ' + [
                  this.coords[0].toPrecision(6),
                  this.coords[1].toPrecision(6)
                ]
            });
          } else {
            myMap.balloon.close();
          }
        })
      })

      axios.get('/api/image?page='+2).then(response => {
        this.coords=response
        console.log(this.coords[0])
      }).catch((error) => {
        console.log(error.response.status)
      })


    })
  },
  methods: {

  }
}
</script>

<style>
#map {
  width: 600px;
  height: 400px;
  margin-left: 35%;
}

.info_paragraph {
  font-size: 25px;
}
.add {
  color: blue;
  text-decoration: none;
}
</style>
